import { Component, ElementRef, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ActivatedRoute } from '@angular/router';
import { RegisterSettlementService } from '../../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { CommonService } from '../../../../../../services/common.service';
import { HeaderService } from '../../../../../../services/header.service';
@Component({
  selector: 'settled-session-list',
  templateUrl: './settled-session-list.component.html',
  styleUrls: ['./settled-session-list.component.scss']
})
export class SettledSessionListComponent {
  searchText: string = '';
  filterParams: string = undefined;
  sort: number = 1;
  totalRecords: number = 0;
  pageNo: number = 1;
  pageSize: number = 6;
  pagin: number;
  pages: any[] = [];
  sessionList: any[] = [];
  branchesList: any[] = [];
  employeesList: any[] = [];
  sessionFilterForm: FormGroup;
  @ViewChild('success') success: ElementRef;
  constructor(private _registerSettlementService: RegisterSettlementService,
    private _activeRoute: ActivatedRoute,
    private _commonService: CommonService,
    private _headerService: HeaderService,
    private fb: FormBuilder, public datepipe: DatePipe, private modalService: NgbModal) {
  }
  initFilterForm() {
    this.sessionFilterForm = this.fb.group({
      register: [''],
      branch: [null],
      createdBy: [null],
      date: [''],
    })
  }
  ngOnInit() {
    this.getAllSessions(this.filterParams);
    this.getBaranches();
    this.getEmployees();
    this.initFilterForm();
    this.sessionFilterForm.valueChanges.subscribe(value => {
      this.pageNo = 0;
      let dateObj = { fromDate: '', toDate: '' };
      let formValues;
      if (value['date']) {
        dateObj['fromDate'] = this.datepipe.transform(value['date'][0], 'yyyy-MM-dd');
        dateObj['toDate'] = this.datepipe.transform(value['date'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...value, ...dateObj };
      delete formValues['date'];
      if (formValues.branch == null) {
        formValues = { ...formValues, branch: '' }
      }
      if (formValues.createdBy == null) {
        formValues = { ...formValues, createdBy: '' }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      this.getAllSessions(this.filterParams)
    })
  }

  resetDate() {
    this.sessionFilterForm.controls['date'].setValue('');
  }
  resetRegisterNo() {
    this.sessionFilterForm.controls['register'].setValue('');
  }
  navigateTo(id) {
    this._commonService.NavigateToRoute(`/unsettled-session/session/${id}`);
  }
  getAllSessions(filterParams = this.filterParams) {
    let defaultParams = `status=16006&pageNo=${this.pageNo-1}&sort=${this.sort}&pageSize=${this.pageSize}`
    this._registerSettlementService.getAllSessions((filterParams && filterParams + `&${defaultParams}`) || (`?${defaultParams}`)).subscribe(response => {
      this.sessionList = response?.data;
      console.log(this.sessionList);
      this.totalRecords = response?.totalRecordCount;
      this.pagin = Math.ceil(this.totalRecords / 6);
      this.pages = _.range(this.pagin);
    })
  }
  getBaranches() {
    this._registerSettlementService.getBranches().subscribe(response => {
      this.branchesList = response.data;
    })
  }
  getEmployees() {
    this._registerSettlementService.getEmployees().subscribe(response => {
      this.employeesList = response.data;
    })
  }
  // Sorting Functions
  sortByBranch() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getAllSessions();
  }
  sortByRegisterNo() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getAllSessions();
  }
  sortByRegisterName() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getAllSessions();
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getAllSessions();
  }
  sortByRequested() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getAllSessions();
  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getAllSessions();
  }

  /**
   * pagination
   * @param page page no
   */
  pageChange(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getAllSessions();
  }

}
