import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddBankDepositsComponent } from './add-bank-deposits/add-bank-deposits.component';
import { ChequeDepositComponent } from './cheque-deposit/cheque-deposit.component';
import { CreateDepositComponent } from './create-deposit/create-deposit.component';
import { SessionsComponent } from './sessions/sessions.component';
import { ViewDepositedChequesComponent } from './view-deposited-cheques/view-deposited-cheques.component';
import { ViewSessionDetailsComponent } from './view-session-details/view-session-details.component';

const routes: Routes = [
  { path: '', component: SessionsComponent },
  { path: 'add-bank-deposit', component: AddBankDepositsComponent },
  { path: 'create-deposit', component: CreateDepositComponent },
  { path: 'cheque-deposit', component: ChequeDepositComponent },
  { path: 'deposited-cheques/:depositId', component: ViewDepositedChequesComponent },
  { path: 'session/:sessionId', component: ViewSessionDetailsComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UnsettledSessionRoutingModule { }
