import { formatDate } from '@angular/common';
import { Component, ElementRef, EventEmitter, Input, Output, SimpleChanges, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as _ from 'underscore';
import { CommonService } from '../../../../../../../services/common.service';
import { ModalMessageComponent } from '../../../../../../../shared/components/modal-message/modal-message.component';
import { ViewUploadedFileComponent } from '../../../../../main-fund/bank-deposits/view-uploaded-file/view-uploaded-file.component';
import { RegisterSettlementService } from '../../../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { CloseRegisterService } from '../../../../close-register/services/close-register.service';

@Component({
  selector: 'app-session-cheques',
  templateUrl: './session-cheques.component.html',
  styleUrls: ['./session-cheques.component.scss']
})
export class SessionChequesComponent {
  pageNo: number = 1;
  dcPageNo: number = 1;
  sort: number = 1;
  pagin!: number;
  pages!: number[];
  searchText: string = '';
  @Input() response;
  sessionCheques: any;
  depositedCheques = [];
  dcTotalRecordCount = 0;
  sessionId;
  chequeDetails: any;
  editCollectionResponse: any;
  bankList: any;
  totalRecordCount: number;
  @Output() loadData = new EventEmitter<any>();
  chequeForm: FormGroup;
  @Input() isSettle = true;
  canManageAndView: boolean;
  //@ViewChild('successModal') successModal: ElementRef;
  constructor(private fb: FormBuilder, private _registerSettlementService: RegisterSettlementService, private _activeRoute: ActivatedRoute,
    private _modalService: NgbModal, private _closeRegisterService: CloseRegisterService, public _commonService: CommonService) {
  }
  ngOnChanges(changes: SimpleChanges) {
    this.response = changes['response'].currentValue;
    this.sessionCheques = this.response?.data;
    this.totalRecordCount = this.response?.totalRecordCount;
    this.pagin = Math.ceil(this.totalRecordCount / 6);
    this.pages = _.range(this.pagin);
  }
  ngOnInit() {
    this._activeRoute.params.subscribe(param => {
      this.sessionId = param['sessionId'];
      this.getDepositedCheques()
    })
  }
  getDepositedCheques() {
    let params = `?sessionId=${this.sessionId}&pageNo=${this.dcPageNo - 1}&pageSize=6`;
    this._registerSettlementService.getSessionDepositedCheques(params).subscribe(response => {
      console.log(response);
      if (response?.data?.length) {
        this.depositedCheques = response?.data;
        this.dcTotalRecordCount = response?.totalRecordCount;
      }
    })
  }
  dcPageChange(page: number) {
    this.dcPageNo = page;
    this.getDepositedCheques();
  }
  viewDepositedAttachment(file) {
    const modalRef = this._modalService.open(ViewUploadedFileComponent);
    modalRef.componentInstance.file = file;
  }
  handleChequeDetails(item, content) {
    this.chequeDetails = {};
    this.chequeDetails = item;
    console.log(this.chequeDetails);
    this._modalService.open(content, { centered: true, size: 'lg' });
  }
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByChequeNo() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByAmount() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByBank() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.loadData.emit({ page: this.pageNo, chequeNumber: this.searchText, sort: this.sort })
  }
  pageChange(page: number) {
    this.pageNo = page;
    this.loadData.emit({ page, chequeNumber: '', sort: 1 });
    window.scroll(0, 0);
  }
  removeSearch() {
    this.searchText = '';
    this.loadData.emit({ page: 1, chequeNumber: '', sort: 1 })

  }
  searchData(event: any) {
    const text = event.target.value;
    if (text.length >= 3) {
      this.searchText = text;
      this.loadData.emit({ page: 1, chequeNumber: text, sort: 1 })
    }
    else if (text.length == 0) {
      this.searchText = '';
      this.loadData.emit({ page: 1, chequeNumber: '',sort:1 })
    }
  }
}
