import { Component, ElementRef, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CollectService } from '../../collect/services/collect.service';
import { AllocationService } from '../../../main-fund/allocation/allocation/allocation-services/allocation.service';
import { SharedService } from '../../../../../services/shared.service';
import { CommonService } from '../../../../../services/common.service';
import { UnsettledSessionService } from '../services/unsettled-session.service';
import { ViewCashDepositsModalComponent } from '../../../main-fund/bank-deposits/view-cash-deposits-modal/view-cash-deposits-modal.component';
import { HeaderService } from '../../../../../services/header.service';
@Component({
  selector: 'app-add-bank-deposits',
  templateUrl: './add-bank-deposits.component.html',
  styleUrls: ['./add-bank-deposits.component.scss']
})
export class AddBankDepositsComponent {
  deposits = [];
  banks = [];
  bankAccounts = [];
  sort: number = 1;
  totalRecords: number = 0;
  pageNo = 1;
  pageSize: number = 6;
  pagin: number;
  pages: any[] = [];
  unsettledSessionsAmount: any[] = [];
  depositId = '';
  amount = '';
  bankAccountId = null;
  bankId = null;
  mainFundAmounts;
  isCash = true;
  allowCreate = false;
  cashDepositTypeId = 0;
  @ViewChild('success') success: ElementRef;
  constructor(
    public _unsettleSession: UnsettledSessionService,
    public _commonService: CommonService,
    public _sharedService: SharedService,
    public _headerService: HeaderService,
    private _allocationService: AllocationService,
    private _collectService: CollectService,
    private _modalService: NgbModal,) {
  }
  ngOnInit() {
    this._headerService.setTitle('Unsettled Session > Add Bank Deposits')
    this.getMainFundCash();
    this.getRegisterUnsettledSessionsAmount();
    this.getRegisterDetails();
    this.getCashDeposits();
    this.getBanks();
    this.getUserPermission();
    this.getBankAccounts();
  }
  getRegisterUnsettledSessionsAmount() {
    this._unsettleSession.getRegisterUnsettledSessionsAmount(this._sharedService.getRegister?.registerObj?.id).subscribe(response => {
      this.unsettledSessionsAmount = response?.data;
    })
  }

  getUserPermission() {
    let permssions = JSON.parse(localStorage.getItem('permissions'));
    let permissionItems = permssions.find(x => x.perCatName == "Main Fund Banks Deposits Permissions")['permissionSubCategories'][0]['permissionItems'];
    this.allowCreate = permissionItems.find(x => x.permissionItemId == 147)['selected'];
  }
  getCashDeposits() {
    let filters = `?registerId=${this._sharedService.getRegister.registerObj?.id}&bankId=${this.bankId && this.bankId || ''}
    &AccountId=${this.bankAccountId && this.bankAccountId || ''}&DepositeId=${this.depositId}&DepositeType=${this.isCash && 'cash' || 'cheque'}
    &amount=${this.amount}&Sort=${this.sort}&PageNo=${this.pageNo - 1}&pageSize=${this.pageSize}`
    this._allocationService.getRegisterDirectDeposits(filters).subscribe((response: any) => {
      this.deposits = response['data'];
      this.totalRecords = response?.totalRecordCount;
    })
  }
  getRegisterDetails() {
    return this._collectService
      .GetRegisterDetails(this._sharedService.getRegister.registerObj?.id).subscribe((response: any) => {
        if (response) {
          this.cashDepositTypeId = response?.['data']['cashDepositTypeId'];
        }
      });
  }
  getCashDepositDetails(id, type) {
    if (type == 'cash') {
      this._allocationService.getRegisterDirectCashDepositDetails(id).subscribe(response => {
        console.log(response);
        const modalRef = this._modalService.open(ViewCashDepositsModalComponent, { size: 'lg' });
        modalRef.componentInstance.depositDetails = response['data'][0];
      })
    }
    else {
      this._commonService.NavigateToRoute('/unsettled-session/deposited-cheques/' + id)
    }
  }
  depositType(event) {
    this.isCash = event.target.value == 'cash' ? true : false;
    this.pageNo = 1;
    this.sort = 1;
    this.getCashDeposits();
  }
  clearAmountInput() {
    this.amount = '';
    this.getCashDeposits();
  }
  clearDepositInput() {
    this.depositId = '';
    this.getCashDeposits();
  }
  onDepositChange() {
    this.getCashDeposits();
  }
  onAmountChange() {
    this.getCashDeposits();
  }
  getBanks() {
    this._unsettleSession.getBanks().subscribe(response => {
      this.banks = response['data'];
    })
  }
  onBankChange(event) {
    this.bankAccounts = [];
    this.bankAccountId = null;
    //this.getBankAccounts(event?.bankId);
    this.getCashDeposits();
  }
  getBankAccounts() {
    //let param = `?bankId=${id}&status=2001&pageNo=0&pageSize=1000`
    let param = `?status=2001&pageNo=0&pageSize=1000`
    this._unsettleSession.getBankAccounts(param).subscribe(response => {
      this.bankAccounts = response['data'];
    })
  }
  onBankAccountChange(event) {
    this.getCashDeposits();
  }
  getMainFundCash() {
    this._allocationService.getMainFundCash().subscribe(response => {
      console.log(response['data']);
      this.mainFundAmounts = response?.data;
    })
  }
  // Sorting Functions
  sortByDepositId() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getCashDeposits()
  }
  sortByBankName() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getCashDeposits()
  }
  sortByAccountName() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getCashDeposits()
  }
  sortByAccountNo() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getCashDeposits()
  }
  sortByAmount() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getCashDeposits()
  }
  /**
   * pagination
   * @param page page no
   */
  pageChange(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getCashDeposits()
  }

}
