
import { Component, ElementRef, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { Status } from '../../../../../shared/enums/enum';
import { CommonService } from '../../../../../services/common.service';
import { UnsettledSessionService } from '../services/unsettled-session.service';
import { RegisterSettlementService } from '../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { SharedService } from '../../../../../services/shared.service';
import { HeaderService } from '../../../../../services/header.service';
import { CollectService } from '../../collect/services/collect.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RegisterCurrenciesComponent } from '../components/register-currencies/register-currencies.component';
@Component({
  selector: 'app-sessions',
  templateUrl: './sessions.component.html',
  styleUrls: ['./sessions.component.scss']
})
export class SessionsComponent {
  searchText: string = '';
  filterParams: string = undefined;
  sort: number = 1;
  totalRecords: number = 0;
  pageNo: number = 1;
  pageSize: number = 6;
  pagin: number;
  pages: any[] = [];
  sessionList: any[] = [];
  unsettledSessionsAmount;
  branchesList: any[] = [];
  employeesList: any[] = [];
  statusList: any[] = [];
  sessionFilterForm: FormGroup;
  sessionDetails: any;
  registerDetails: any;
  status = Status;
  radioValue: any;
  response: any;
  message = '';
  cashDepositTypeId = 0;
  @ViewChild('success') success: ElementRef;
  constructor(
    public _unsettleSession: UnsettledSessionService,
    private _registerSettlementService: RegisterSettlementService,
    public _commonService: CommonService,
    public _sharedService: SharedService,
    public _headerService: HeaderService,
    public _collectService: CollectService,
    public _modalService: NgbModal,
    private fb: FormBuilder, public datepipe: DatePipe) {
  }
  initFilterForm() {
    this.sessionFilterForm = this.fb.group({
      sessionId: [''],
      createdBy: [null],
      status: [null],
    })
  }
  ngOnInit() {
    this._headerService.setTitle('Sessions Summary')
    this.getAllSessions(this.filterParams);
    this.getEmployees();
    this.getStatusList(16);
    this.initFilterForm();
    this.getRegisterUnsettledSessionsAmount();
    this.getRegisterDetails();
    this.sessionFilterForm.valueChanges.subscribe(value => {
      let formValues;
      formValues = { ...value };
      let isEmpty = true;
      for (let obj in value) {
        if (value[obj]) {
          this.pageNo = 1;
          isEmpty = false;
        }
        if (formValues[obj] == null) {
          formValues[obj] = ''
        }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      if (isEmpty) {
        this.filterParams = undefined;
        this.pageNo = 1;
      }
      this.getAllSessions(this.filterParams)
    })
  }
  handleTabSelection(session) {
    //if (session == 'settled')
    //  this._headerService.setTitle('Settled Session')
    //else
    //  this._headerService.setTitle('Unsettled Session')
  }
  getRegisterDetails() {
    return this._collectService
      .GetRegisterDetails(this._sharedService.getRegister.registerObj?.id).subscribe((response: any) => {
        if (response) {
          this.cashDepositTypeId = response?.['data']['cashDepositTypeId'];
        }
      });
  }
  resetRegisterNo() {
    this.sessionFilterForm.controls['sessionId'].setValue('');
  }
  viewRegisterCurrencies(item) {
    const ref = this._modalService.open(RegisterCurrenciesComponent, { size: 'md' });
    ref.componentInstance.data = { registerCurrencies: item?.registerCurrencies };
  }
  getAllSessions(filterParams = this.filterParams) {
    //show only sessions with statusId = 16002,16004,16005 OnInit;
    let defaultParams = `status=16002,16004,16005&register=${this._sharedService.getRegister?.registerObj?.id}&pageNo=${this.pageNo - 1}&sort=${this.sort}&pageSize=${this.pageSize}`
    this._unsettleSession.getRegisterUnsettledSessions((filterParams && filterParams + `&${defaultParams}`) || (`?${defaultParams}`)).subscribe(response => {
      this.sessionList = response?.data;
      this.totalRecords = response?.totalRecordCount;
      this.pagin = Math.ceil(this.totalRecords / 6);
      this.pages = _.range(this.pagin);
    })
  }
  getRegisterUnsettledSessionsAmount() {
    this._unsettleSession.getRegisterUnsettledSessionsAmount(this._sharedService.getRegister?.registerObj?.id).subscribe(response => {
      this.unsettledSessionsAmount = response?.data;
    })
  }
  getEmployees() {
    this._unsettleSession.getUserCashier().subscribe(response => {
      this.employeesList = response.data;
    })
  }
  getStatusList(id) {
    this._registerSettlementService.getStatusList(id).subscribe(response => {
      this.statusList = response.data.filter(x => x.id == 16002 || x.id == 16004 || x.id == 16005).map(x => { return { id: x.id, description: x.description } });
      console.log(this.statusList);
    })
  }
  // Sorting Functions
  sortBySession() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getAllSessions();
  }
  sortByDate() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getAllSessions();
  }
  sortBySessionTime() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getAllSessions();
  }
  sortByEmployee() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getAllSessions();
  }
  sortByStatus() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getAllSessions();
  }
  /**
   * pagination
   * @param page page no
   */
  pageChange(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getAllSessions();
  }

}

