import { DatePipe, formatDate } from '@angular/common';
import { Component, ElementRef, EventEmitter, Input, Output, SimpleChanges, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as _ from 'underscore';
import { CommonService } from '../../../../../../../services/common.service';
import { ModalMessageComponent } from '../../../../../../../shared/components/modal-message/modal-message.component';
import { RegisterSettlementService } from '../../../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { CloseRegisterService } from '../../../../close-register/services/close-register.service';
import { CollectService } from '../../../../collect/services/collect.service';

@Component({
  selector: 'app-session-card-payments',
  templateUrl: './session-card-payments.component.html',
  styleUrls: ['./session-card-payments.component.scss']
})
export class SessionCardPaymentsComponent {
  pageNo: number = 1;
  sort: number = 1;
  pagin!: number;
  pages!: number[];
  searchText: string = '';
  @Input() response;
  sessionCards: any;
  totalRecordCount: number;
  @Output() loadCardData = new EventEmitter<any>();
  bankList: any;
  cardForm: FormGroup;
  cardDetails: any;
  @Input() isSettle = true;
  editCollectionResponse: any;
  canManageAndView: boolean;
  terminals = [];
  constructor(private fb: FormBuilder, private _registerSettlementService: RegisterSettlementService,
    private datePipe: DatePipe, private _collectService: CollectService,
    private _modalService: NgbModal, private _closeRegisterService: CloseRegisterService, public _commonService: CommonService) {
  }
  ngOnChanges(changes: SimpleChanges) {
    this.response = changes['response'].currentValue
    this.sessionCards = this.response?.cards;
    this.totalRecordCount = this.response?.totalRecordCount;
    this.pagin = Math.ceil(this.totalRecordCount / 6);
    this.pages = _.range(this.pagin);
  }
  ngOnInit() {
  }
  get formValid() {
    return this.cardForm.controls;
  }
  handleCardDetails(item, content) {
    this.cardDetails = {};
    this.cardDetails = item;
    this._modalService.open(content, { centered: true, size: 'lg' });
  }
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  sortByCardType() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  sortByLastFour() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  sortByInvoice() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  sortByTerminal() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  sortByAmount() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  sortByStatus() {
    if (this.sort == 15) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 14 ? 15 : 14;
    }
    this.loadCardData.emit({ page: this.pageNo, customer: this.searchText, sort: this.sort })
  }
  pageChange(page: number) {
    this.pageNo = page;
    this.loadCardData.emit({ page, customer: '', sort: 1 });
    window.scroll(0, 0);
  }
  removeSearch() {
    this.searchText = '';
    this.loadCardData.emit({ page: 1, customer: '', sort: 1 })
  }
  searchData(event: any) {
    const text = event.target.value;
    if (text.length >= 3) {
      this.searchText = text;
      this.loadCardData.emit({ page: 1, customer: text })
    }
    else if (text.length == 0) {
      this.searchText = '';
      this.loadCardData.emit({ page: 1, customer: '' })
    }
  }
}
