import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import * as _ from 'underscore';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { CollectService } from '../../../../collect/services/collect.service';
import { SharedService } from '../../../../../../../services/shared.service';
import { CloseRegisterService } from '../../../../close-register/services/close-register.service';
import { RegisterSettlementService } from '../../../../../main-fund/register-settlements/register-settlements-service/register-settlement.service';
import { ViewUploadedFileComponent } from '../../../../../main-fund/bank-deposits/view-uploaded-file/view-uploaded-file.component';

@Component({
  selector: 'app-session-bank-pledges',
  templateUrl: './session-bank-pledges.component.html',
  styleUrls: ['./session-bank-pledges.component.scss']
})
export class SessionBankPledgesComponent {
  banks = [];
  payerName = '';
  sort: number = 1;
  sessionId: number;
  pageNo: number = 1;
  searchText: string = '';
  formGroup: FormGroup;
  sessionData: any;
  details: any;
  fileDetails = { fileName: '', file: '' }
  totalAllRecordsCount: number;
  totalRecordsCount: number;
  @Input() isSettle: boolean;
  @Output() loadMainData = new EventEmitter<string>();
  constructor(
    public _registerSettlementService: RegisterSettlementService,
    private activeRoute: ActivatedRoute,
    public closeRegisterService: CloseRegisterService,
    public sharedService: SharedService,
    private _modalService: NgbModal,
    private fb: FormBuilder,
    private _collectService: CollectService,
  ) { }

  ngOnInit(): void {
    this.activeRoute.params.subscribe(params => {
      this.sessionId = params['sessionId'];
      this.getSessionBankPledge();
    })
  }
  handleBankTransferDetails(item, content) {
    this.details = item;
    this.fileDetails.file = item?.pledgeAttachment;
    this.fileDetails.fileName = this.getFilName(item?.pledgeAttachment);
    this._modalService.open(content, { size: 'lg', centered: true });
  }
  getFilName(name) {
    if (name)
      return name?.split('/').pop()
  }
  viewFile() {
    const modalRef = this._modalService.open(ViewUploadedFileComponent, { size: 'md' });
    modalRef.componentInstance.file = this.fileDetails?.file;
  }
  getSessionBankPledge() {
    let obj = `?sessionId=${this.sessionId}&search=${this.searchText}&pageNo=${this.pageNo - 1}&pageSize=6&sort=${this.sort || 1}`;
    return this._registerSettlementService
      .getSessionBankPledge(obj)
      .subscribe((response: any) => {
        if (response) {
          this.sessionData = response.data;
          console.log(this.sessionData);
          this.totalAllRecordsCount = response?.totalAllRecordsCount;
          this.totalRecordsCount = response?.totalRecordCount;
        }
      });
  }

  pageChange(page: number) {
    this.pageNo = page;
    this.getSessionBankPledge();
    window.scroll(0, 0);
  }

  searchData(event: any) {
    const text = event.target.value;
    if (text.length >= 1) {
      this.searchText = text;
      this.pageNo = 1;
      this.getSessionBankPledge();
    }
    if (text.length == 0) {
      this.pageNo = 1;
      this.getSessionBankPledge();
    }
  }
  removeSearch() {
    this.searchText = '';
    this.getSessionBankPledge();
  }
  //SORTING
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getSessionBankPledge();
  }
  sortByPledgeNumber() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getSessionBankPledge();
  }
  sortByAmount() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getSessionBankPledge();
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getSessionBankPledge();
  }
  sortByBank() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getSessionBankPledge();
  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getSessionBankPledge();
  }

}
